* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
html {
  overflow-x: hidden;
}

.container-login {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background-image: url("/dist/images/bg5.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  .navbar-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    background-color: rgba(30, 143, 255, 0.747);

    .links {
      display: flex;
      flex: 0.5;
      height: 100%;
      justify-content: center;
      align-items: center;

      .nav-item {
        display: flex;
        flex: 1.25;
        height: 100%;
        justify-content: center;
        align-items: center;
        a {
          color: rgb(255, 255, 255);
          text-decoration: none;
          font-size: 30px;

          &:hover {
            color: rgb(209, 209, 209);
            border-bottom: 1px solid white;
          }
        }
      }
    }

    .logo {
      display: flex;
      flex: 1;
      justify-content: center;
      align-items: center;
      height: 100%;
      .logo2 {
        display: flex;
        flex: 1;
        height: 80%;
        h1 {
          display: flex;
          flex: 1;
          height: 100%;
          margin-left: -24px;
          justify-content: flex-start;
          align-items: center;
          letter-spacing: 2px;
          color: rgb(235, 235, 235);
        }
        img {
          width: 140px;
          height: 95%;
        }
      }
    }

    .empty {
      display: flex;
      flex: 0.9;
      height: 100%;
      justify-content: center;
      align-items: center;
    }
  }

  .main-content {
    display: flex;
    flex-direction: column;
    align-content: space-evenly;
    justify-content: flex-start;
    flex: 1;
    padding: 36px;

    .main-title {
      display: flex;
      justify-content: center;
      text-align: center;
      flex: 0.5;
      color: rgb(221, 221, 221);
    }

    .main-btn {
      display: flex;
      justify-content: center;
      text-align: center;
      width: 100%;
      button {
        height: 50px;
        width: 120px;
        font-size: 16px;
      }
      .btn-primary {
        font-size: 1.5rem;
      }
    }

    .main-preview {
      display: flex;
      justify-content: center;
      text-align: center;
      flex: 1.5;
      margin: auto;
      width: 50%;

      .preview {
        display: flex;
        flex: 1;
        background-image: url("/dist/images/map-preview.png");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        background-origin: content-box;
        margin-bottom: 24px;
        -webkit-box-shadow: 0px 0px 2px 3px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 0px 2px 3px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 0px 2px 3px rgba(0, 0, 0, 0.75);
      }
    }
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  .howto-modal {
    display: none;
    position: absolute;
    justify-content: center;
    align-items: center;
    top: 10vh;
    left: 0;
    height: 90vh;
    width: 100%;
    animation-name: fadeIn;
    animation-duration: 1s;
    background-color: rgba(90, 88, 88, 0.671);
    .close-modal {
      position: absolute;
      top: 80vh;
      right: 1vw;
      button {
        font-size: 20px;
      }
    }
    .howto-content {
      display: flex;
      justify-content: center;
      align-content: center;
      width: 70%;
      height: 70%;
      background-color: rgb(214, 214, 214);
      border-radius: 24px;
      padding: 20px;
      .step1 {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        flex: 0.5;
        color: rgb(54, 54, 54);
        border-right: 2px solid rgb(48, 48, 48);
        span {
          font-size: 36px;
          text-decoration: underline;
        }
        .explain {
          display: flex;
          text-align: center;
          flex-direction: column;
          font-size: 28px;
          justify-content: center;
          align-items: center;
          width: 90%;
          flex: 1;
          span {
            font-size: 20px;
            text-decoration: none;
          }
        }
      }
      .step2 {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        flex: 0.5;
        color: rgb(54, 54, 54);
        span {
          font-size: 36px;
          text-decoration: underline;
        }
        .explain {
          display: flex;
          text-align: center;
          flex-direction: column;
          font-size: 28px;
          justify-content: center;
          align-items: center;
          width: 90%;
          flex: 1;
          span {
            font-size: 20px;
            text-decoration: none;
          }
        }
      }
    }
  }
}
